@font-face {
  font-family: DMSans-Bold;
  src: url("DMSans-Bold.51beb29d.ttf");
}

@font-face {
  font-family: DMSans-Medium;
  src: url("DMSans-Medium.0957dae6.ttf");
}

@font-face {
  font-family: DMSans-MediumItalic;
  src: url("DMSans-MediumItalic.574d41d5.ttf");
}

@font-face {
  font-family: DMSans-Regular;
  src: url("DMSans-Regular.23c41658.ttf");
}

@font-face {
  font-family: DMSans-RegularItalic;
  src: url("DMSans-Italic.b1eebbd6.ttf");
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input, textarea {
  outline: none;
}

.wrapper {
  grid-template: "header" 72px
                 "main" 1fr
                 / 1fr;
  row-gap: 40px;
  height: 100%;
  display: grid;
}

.wrapper__header {
  grid-area: header;
}

.main {
  grid-area: main;
}

.wrapper__sidebar {
  grid-area: sidebar;
  justify-items: center;
}

.wrapper__content {
  grid-area: content;
}

@media (min-width: 768px) {
  .wrapper {
    grid-template: "header" 72px
                   "main" 1fr
                   / 1fr;
    height: 100%;
    display: grid;
  }

  .main {
    grid-template: "sidebar" 472px
                   "content" 1fr
                   / 1fr;
    row-gap: 100px;
    width: 100%;
    display: grid;
  }

  .wrapper__sidebar {
    grid-template-rows: 472px;
    grid-template-columns: 337px 335px;
    column-gap: 32px;
    display: grid;
  }
}

@media (min-width: 1440px) {
  .wrapper {
    grid-template: "header" 72px
                   "main" 1fr
                   / 1fr;
    height: 100%;
    display: grid;
  }

  .main {
    grid-template: "sidebar content" 1fr
    / 356px 1fr;
    column-gap: 40px;
    display: grid;
  }

  .wrapper__sidebar {
    grid-template-rows: 472px 490px;
    grid-template-columns: 1fr;
    row-gap: 86px;
  }

  .main.shopping-list__wrapper {
    align-items: start;
    column-gap: 40px;
  }
}

@media (max-width: 1439px) {
  .main.shopping-list__wrapper {
    display: block;
  }

  .wrapper__sidebar.shopping-list__wrapper {
    display: none;
  }
}

*, :after, :before {
  box-sizing: border-box;
}

body, html {
  height: 100%;
  margin: 0;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: DMSans-Regular;
  font-size: 14px;
  font-style: normal;
}

:root {
  --brand-color: #4f2ee8;
  --text-color: #111;
  --subtitle-color: #b4afaf;
  --categories-rgba-color: rgba(17, 17, 17, .6);
  --background-color: #f6f6f6;
  --bg-color-header-card: #fff;
  --button-sign-up-color: #111;
  --button-sign-up-font-color: #f3f3f3;
  --button-yelow-color: #eac645;
  --button-yelow-font-color: #111;
  --gray-button-color: #f3f3f3;
  --gray-rgba-color: rgba(246, 246, 246, .5);
  --white-color: #fff;
  --yelow-color: #eac645;
}

[dark] {
  --brand-color: #4f2ee8;
  --text-color: #fff;
  --subtitle-color: #b4afaf;
  --categories-rgba-color: rgba(255, 255, 255, .6);
  --background-color: #202024;
  --bg-color-header-card: #111;
  --button-sign-up-color: #f6f6f6;
  --button-sign-up-font-color: #202024;
  --button-yelow-color: #eac645;
  --button-yelow-font-color: #111;
  --gray-button-color: #f3f3f3;
  --white-color: #fff;
  --yelow-color: #eac645;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
