  letter-spacing: -.36px;
  font-family: DMSans-Regular;
  font-size: 18px;
  line-height: 1.33;
  transition: color .25s linear;
}

.categories__title:hover, .categories__title:focus {
  color: var(--brand-color);
  text-transform: uppercase;
  font-family: DMSans-Bold;
  font-weight: 700;
}

.categories__title-active {
  color: var(--brand-color);
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DMSans-Bold;
  font-size: 18px;
  line-height: 1.33;
}

.book-title-in-modal {
  color: var(--text-color);
  letter-spacing: -.96px;
  font-family: DMSans-Bold;
  font-size: 16px;
  line-height: 1.12;
}

.button-modal {
  color: var(--text-color);
  letter-spacing: -.18px;
  text-transform: uppercase;
  font-family: DMSans-Bold;
  font-size: 18px;
  line-height: 1.33;
}

.button {
  color: var(--text-color);
  border: 2px solid var(--brand-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  font-family: DMSans-Bold;
  transition: background-color .25s linear, color .25s linear;
}

.button:hover, .button:focus {
  color: var(--white-color);
  background-color: var(--brand-color);
}

.filter-img {
  filter: brightness(0) invert();
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal {
  text-align: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: var(--background-color);
  z-index: 30;
  border: 2px solid #111;
  border-radius: 18px;
  width: 100%;
  max-width: 335px;
  padding: 40px 24px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 579px;
    padding: 40px;
  }
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  overflow: hidden;
}

.backdrop {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(17, 17, 17, .4);
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  overflow: hidden;
}

.backdrop-login {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #4f2ee8;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.backdrop-login.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.filter-img {
  filter: brightness(0) invert();
}

