.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 9px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  border-radius: 10px;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background-image: linear-gradient(#4f2ee8, #686868);
}

input:checked + .slider:before {
  transform: translateX(20px);
}

