  0% {
    -moz-transform: translateZ(0)scale(.75, 1);
  }

  25% {
    -moz-transform: translateZ(0)scale(1.5, .5);
  }

  50% {
    -moz-transform: translateZ(0)scale(.75, 1);
  }

  75% {
    -moz-transform: translateZ(0)scale(1.5, .5);
  }

  100% {
    -moz-transform: translateZ(0)scale(.75, 1);
  }
}

.scroll-btn {
  background-color: var(--brand-color);
  fill: var(--white-color);
  cursor: pointer;
  z-index: 1;
  opacity: 0;
  border: 5px solid #c3b9f1;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  transition: all .6s ease-in-out;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(100px);
}

@media screen and (max-width: 768px) {
  .scroll-btn {
    left: 30px;
    right: 0;
  }
}

.show-scroll-btn {
  opacity: 1;
  transform: translateY(0);
}



